'use client';

import { ProfileProvider } from '@/contexts/ProfileContext';

export default function DirectorLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <ProfileProvider
      defaultProfile="director"
      defaultUserName="Director Martinez"
      defaultUserInitials="DM"
    >
      {children}
    </ProfileProvider>
  );
}


